import React, { useState } from 'react';
import { useForm } from "react-hook-form";
import { useHistory } from "react-router-dom";
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Button from '@material-ui/core/Button';
import './DialogMat.css';
import axios from 'axios';
import config from '../Environment'

function DialogMat(props) {
    const { register, handleSubmit, errors } = useForm();
    const [open, setOpen] = useState(false);
    const option = {
        headers: { Authorization: `Bearer ${localStorage.getItem("token")}` }
    };
    const history = useHistory();

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const onSubmit = (data) => {
        axios.post(`${config.apiUrl}/todos/`,{...data},option)
        .then((data=>{
            setOpen(false)
            history.push("/");
        }))
        .catch((error)=>{
            alert(error)
        })
    }
    return (
        <div >
            <Button className="font-weight-bold" variant="outlined" color="primary" onClick={handleClickOpen}>
                {props.children}
            </Button>
            <Dialog fullWidth open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                <form onSubmit={handleSubmit(onSubmit)} noValidate autoComplete="off">
                    <DialogContent>
                        <DialogContentText>
                            Title
                        </DialogContentText>
                        <input
                            autoFocus
                            id="title"
                            name="title"
                            type="text"
                            className="fullWidth"
                            ref={register({
                                required: {
                                    value: true,
                                    message: "Please enter title",
                                },
                            })}

                        />
                        {errors.title && (
                            <span className="errorMessage d-block">
                                {errors.title.message}
                            </span>
                        )}
                        <DialogContentText className="mt-2">
                            Description
                        </DialogContentText>
                        <textarea 
                        id="description"
                        name="description"
                        className="fullWidth"
                        ref={register({
                            required:false
                        })}
                        />
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={handleClose} color="primary">
                            Cancel
                    </Button>
                        <Button type="submit" color="primary">
                            Create
                    </Button>
                    </DialogActions>
                </form>
            </Dialog>
        </div>
    )
}

export default DialogMat
