import React, { useState , useEffect } from 'react';
import { useForm } from "react-hook-form";
import { useHistory } from "react-router-dom";
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import Button from '@material-ui/core/Button';
import './DialogEdit.css';
import axios from 'axios';
import config from '../Environment'
import EditIcon from '@material-ui/icons/Edit';
function DialogEdit(props) {
    const { register, handleSubmit, errors } = useForm();
    const [open, setOpen] = useState(false);
    const [dataedit , setDataedit] = useState([])
    const option = {
        headers: { Authorization: `Bearer ${localStorage.getItem("token")}` }
    };
    const history = useHistory();
    const getData =() =>{
        axios.get(`${config.apiUrl}/todos/${props.id}`, option)
        .then((data) => {
            setDataedit({
                title:data.data.title,
                description:data.data.description
            })
        })
        .catch((error) => { alert(error) })
    }
    const handleClickOpen = () => {
        setOpen(true);
        getData()
    };

    const handleClose = () => {
        setOpen(false);
    };

    const onSubmit = (data) => {
        axios.put(`${config.apiUrl}/todos/${props.id}`,{...data},option)
        .then((data=>{
            setOpen(false)
            history.push("/");
        }))
        .catch((error)=>{
            alert(error)
        })
    }
    return (
        <div >
            <Button onClick={handleClickOpen}>
                <EditIcon />
            </Button>
            <Dialog fullWidth open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                <form onSubmit={handleSubmit(onSubmit)} noValidate autoComplete="off">
                    <DialogContent>
                        <DialogContentText>
                            Title
                        </DialogContentText>
                        <input
                            autoFocus
                            id="title"
                            name="title"
                            type="text"
                            className="fullWidth"
                            value={dataedit.title || ''}
                            onChange={e => setDataedit({ ...dataedit, title: e.target.value })}
                            ref={register({
                                required: {
                                    value: true,
                                    message: "Please enter title",
                                },
                            })}

                        />
                        {errors.title && (
                            <span className="errorMessage d-block">
                                {errors.title.message}
                            </span>
                        )}
                        <DialogContentText className="mt-2">
                            Description
                        </DialogContentText>
                        <textarea 
                        id="description"
                        name="description"
                        className="fullWidth"
                        value={dataedit.description}
                        onChange={e => setDataedit({ ...dataedit, description: e.target.value })}
                        ref={register({
                            required:false
                        })}
                        />
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={handleClose} color="primary">
                            Cancel
                    </Button>
                        <Button type="submit" color="primary">
                            Create
                    </Button>
                    </DialogActions>
                </form>
            </Dialog>
        </div>
    )
}

export default DialogEdit
