import React, { useState, useEffect } from 'react'
import axios from 'axios'
import config from '../Environment'
import styles from './Listcard.module.css'
import DialogMat from '../Dialog/DialogMat'
import DialogEdit from '../DialogEdit/DialogEdit'
import DeleteIcon from '@material-ui/icons/Delete';
const ListCard = () => {
    const [todolist, setTodolist] = useState([])
    const [edit , setEdit] = useState(false)
    const option = {
        headers: { Authorization: `Bearer ${localStorage.getItem("token")}` }
    };
    useEffect(() => {
        getData()
    },[])
    const getData =() =>{
        axios.get(`${config.apiUrl}/todos/`, option)
        .then((data) => {
            setTodolist(data.data)
        })
        .catch((error) => { alert(error) })
    }
    const deleteTodo = (id) =>{
        if(window.confirm('Confirm Delete')){
            axios.delete(`${config.apiUrl}/todos/${id}`,option)
            .then((data=>{
                getData()
            }))
            .catch((error) =>{
            })
        }
    }
    const updateTodo = () =>{

    }
    return (
        <div className="pt-4">
            {todolist != '' ?
                <div className={`${styles.dataBox}`}>
                    {
                        todolist.map((item, index) => (
                            <div className={`${styles.listBox} d-block rounded alert alert-success `} key={item._id} onClick={updateTodo}>
                                <div className="d-flex"><h4 style={{flex:'1'}}>{item.title}</h4> 
                                <DialogEdit id={item._id}/>
                                <button className={`${styles.trashButton} rounded`} onClick={() => deleteTodo(item._id)}><DeleteIcon /></button>
                                </div>
                                <hr></hr>
                                <p className="d-block">{item.description}</p>
                            </div>
                        ))
                    }
                </div> :
                <div className={`${styles.emptyData} ${styles.boxScreen} d-flex text-center mb-4`}>
                    <h1>Empty press 'Create' For add new todo</h1>
                </div>
            }
            <div className="d-flex justify-content-center mt-4">
                <DialogMat>+ Create</DialogMat>
            </div>
        </div>
    )
}

export default ListCard
